html,
body{
  background: var(--color-bg);
  font-family: 'Manrope';
  font-size: 18px;
  font-weight: 400;
  color: var(--color-text-main);
  line-height: 155.556%;
  &.overflow{
    overflow: hidden;
  }
  *{
    box-sizing: border-box;
    transition: all .35s ease;
    &:before,
    &:after{
      transition: all .35s ease;
    }
  }
  ul,
  ol{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  img,
  svg{
    display: block;
    width: 100%;
  }
  svg{
    display: block;
    width: 100%;
    height: 100%;
    fill: none;
    stroke: none;
  }
  a{
    color: var(--color-text-main);
    text-decoration: none;
    &:hover{
      color: var(--color-text-hover);
    }
  }
  button{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: 0;
    border: 0;
    background: var(--color-transparent);
    &:hover{
      color: var(--color-text-hover);
    }
    cursor: pointer;
  }
  b{
    font-weight: 800;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6{
    padding: 0;
    margin: 0;
  }
  .h1, h1,
  .h2, h2,
  .h3, h3,
  .h4, h4,
  .h5, h5,
  .h6, h6{
    letter-spacing: -1px;
  }
  .h1, h1{
    font-size: 48px;
    font-weight: 600;
    line-height: 125%;
  }
  .h2, h2{
    font-size: 32px;
    font-weight: 600;
    line-height: 125%;
  }
  .h3, h3{
    font-size: 24px;
    font-weight: 600;
    line-height: 125%;
  }
  form{
    label{
      display: block;
      padding: 0 0 10px;
      font-size: 14px;
      font-weight: 700;
      line-height: 24px;
    }
    textarea,
    input{
      display: block;
      max-width: 100%;
      min-width: 100%;
      padding: 14px 20px;
      border: 1px solid var(--color-white);
      border-radius: 10px;
      background: var(--color-white);
      font-size: 14px;
      line-height: 24px;
      outline: none;
      &::placeholder{
        color: var(--color-text-gray);
      }
    }
    textarea{
      max-width: 140px;
      min-height: 140px;
      &.big{
        max-width: 250px;
        min-height: 250px;
      }
    }
    input{
      height: 50px;
      &[type="checkbox"]{
        display: none;
        &:checked{
          & + label{
            &:before{
              border-color: var(--color-text-hover);
              background: var(--color-text-hover);
            }
            &:after{
              transform: rotate(45deg) scaleY(1);
            }
          }
        }
        & + label{
          position: relative;
          display: flex;
          align-items: center;
          gap: 8px;
          &:before,
          &:after{
            display: block;
            content: "" ;
          }
          &:before{
            max-width: 16px;
            min-width: 16px;
            height: 16px;
            border: 1px solid var(--color-created);
            border-radius: 2px;
            background: var(--color-white);
          }
          &:after{
            position: absolute;
            top: 6px;
            left: 6px;
            width: 4px;
            height: 8px;
            border: 0 solid var(--color-white);
            border-width: 0 2px 2px 0;
            transform: rotate(45deg) scaleY(0);
            transform-origin: center;
          }
        }
      }
    }
  }
  p,
  .desc{
    padding: 0;
    margin: 0;
    font-size: 14px;
  }
  p{
    padding: 0 0 20px;
    &:last-child{
      padding: 0;
    }
  }
  .br{
    border-radius: 15px;
  }
  .btn{
    display: inline-flex;
    padding: 12px 31px;
    border-radius: 50px;
    font-size: 16px;
    font-weight: 700;
    color: var(--color-white);
    text-decoration: none;
    &.blue{
      border: 1px solid var(--color-text-main);
      background: var(--color-text-main);
    }
    &.white{
      border-color: var(--color-white);
      background: var(--color-white);
      font-weight: 600;
      color: var(--color-text-main);
      &:hover{
        border-color: var(--color-text-main);
        background: var(--color-text-main);
        color: var(--color-white);
      }
    }
    &.transparent{
      border: 1px solid var(--color-text-gray);
      background: var(--color-transparent);
      color: var(--color-text-gray);
    }
    &:active{
      opacity: .8;
    }
    &:hover{
      border-color: var(--color-text-hover);
      background: var(--color-text-hover);
      color: var(--color-white);
    }
  }
  .app{
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 100Vh;
    padding: 115px 0 0;
    overflow-x: hidden;
    &.main_page{
      main.content{
        .block2{
          margin: 17px 0 0;
        }
        .block3,
        .block5,
        .block6,
        .block7,
        .block9,
        .block10{
          margin: 130px 0 0;
        }
        .block8{
          margin: 120px 0 0;
        }
        .block4{
          margin: 49px 0 0;
        }
      }
    }
    &.pharmacovigilance_page{
      main.content{
        .block12{
          margin: 85px 0 0;
        }
        .block13,
        .block14{
          margin: 16px 0 0;
        }
        .block15,
        .block17,
        .block18,
        .block19{
          margin: 100px 0 0;
        }
        .block20{
          margin: 118px 0 0;
        }
      }
    }
    main.content{
      flex: 1 1 auto;
      padding-bottom: 100px;
    }
    .wrap{
      width: 100%;
      padding: 0 48px;
    }
  }

}